.ddb-add-to-searchlist__add {
    /* Trying to hit the actual size on the ddb site.*/
    height: 25px;
    width: 25px;
}

.ddb-add-to-searchlist__star {
    width: 100%;
    height: 100%;
    fill: #000;
}

.ddb-add-to-searchlist__star .ddb-icon__inner-star {
    fill: transparent;
}

.ddb-add-to-searchlist__star:hover .ddb-icon__inner-star {
    fill: #000;
}

.ddb-add-to-searchlist__content {
    display: flex;
    flex-direction: column;
    flex: 1;
}

@media (min-width: 672px) {
    .ddb-add-to-searchlist__content {
        flex-direction: row;
    }
}

.ddb-add-to-searchlist__action {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.ddb-add-to-searchlist__input-container {
    margin: 32px 0 16px 0;
}

@media (min-width: 672px) {
    .ddb-add-to-searchlist__input-container {
        margin: 0 0 16px 0;
        min-width: 350px;
    }
}

.ddb-add-to-searchlist__button {
    min-width: 190px;
}

.ddb-add-to-searchlist__info {
    flex: 1;
    margin-right: 16px;
}

.ddb-add-to-searchlist__help {
    display: block;
    color: #262627;
    font-weight: bold;
    font-size: 1.125rem;
    max-width: 450px;
}

.ddb-add-to-searchlist__success {
    display: flex;
    flex-direction: column;
}

.ddb-add-to-searchlist__success a {
    margin-top: 8px;
    margin-bottom: 8px;
}
